import React from "react";
import styled from "styled-components";
import catImg from '../assets/images/cats.png'

const Image = styled.img`
  max-width: 250px;
  height: auto;
`;

const Container = styled.div`
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 35px;
`;

const Text = styled.p`
    margin-top: 10px;
    font-size: 24px;
    text-align: center;
    color: #171717;
    font-weight: bold;
`;

const NotFound = () => {
  return (
    <Container>
      <Image src={catImg} alt="Images" />
      <Text>404 Not Found</Text>
    </Container>
  );
};

export default NotFound;
